import React from 'react';


import profilePlaceHolder from '../../images/intstractor-profile/pp.png';
import {Row , Col} from 'react-bootstrap';

export default function listItem(props) {
    return(
    <Row className="border-list mb-2 text-center-responsive">
        <Col md={10}>
            <img width="37px" height="37px" className="rounded-circle " src={profilePlaceHolder} alt="profile placeholder"/>
            <p className="d-inline-block ml-2 ">You received a payment of $134.00 from Ahmed Abdelfattah</p>
        </Col>
        <Col md={2}><p className="text-grey ">14 min ago</p></Col>
    </Row>)
}