import React , {useState ,  useEffect} from 'react';
import $ from 'jquery';
import Loader from 'react-loader-spinner';


import PopUp from '../../pop-up/pop-up.component';
import text from '../../../languages/english/translate.json';
import {SESSION_CANCEL , USER_SESSIONS , SESSION_RESCEDUAL} from '../apis/apis';
import {Url} from '../../../url';

import Pp from '../../../images/intstractor-profile/pp.png';
// import more from '../../../images/instractor-home/more.svg';
import clock from '../../../images/icons/clock.svg';
import {Row , Col } from 'react-bootstrap';
import { FormControl , InputLabel , Select} from '@material-ui/core';



export default function SessionsCard(props){
    const [state , setState] = useState({
        showStartSession : false , 
        showRescedual : false ,
        loading : false , 
        error : ''
    });
    let data = props.data;
    let textPage = text.student.instractorProfile.reserveSession.calendar;
    useEffect (()=>{

        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            data &&  $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: USER_SESSIONS,
                    variables : { 
                        orderBy : "ASC" ,
                        rate_minutes_type_id : data.rate_minutes_type.id,
                        course_type_id : data.course_type.id,
                        rate_salary : data.rateSalary
                    }
                },
                dataType: 'json',
                success: res => {
                    let arrOfDates = [] , flag = 1 , sessionsForReschedual = [];
                    let arrOfSession = res.data.meStudents.sessionsGet.data.filter(item=>item.instructor.id === data.instructor.id);
                    arrOfSession && arrOfSession[0].instructor.sessionsByUnits.map(session =>{
                        if(session.sessions.length > 0){
                            sessionsForReschedual.push(session.sessions);
                            arrOfDates.push(session.sessions[0].date);
                        }
                    })
                    let uniqueArr = arrOfDates.filter(onlyUnique);

                    setState({...state , sessionsForReschedual : sessionsForReschedual , arrOfDates : uniqueArr});
                    if(  !uniqueArr > 0  ){
                        setState({...state , error : 'Please try to enter another date.'});
                    }
                },
                cache: err => console.log(err)
            });
        }
    },[props]);
    const onlyUnique =(value, index, self)=> {
        return self.indexOf(value) === index;
      }
    const handleCloseStartSession = () => setState({...state , showStartSession : false});
    const handleCancelReservedSession = () => {
        setState({...state , loading : true});
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: SESSION_CANCEL,
                    variables : {id : data.id}
                },
                dataType: 'json',
                success: res => {
                    setState({...state , loading : false , showStartSession : false});
                    if(res.data.sessionsCancel.Status.check ==  true){
                        props.getUpcomingSessions();
                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    const handleStartSession = () => {
        console.log("start it please!")
    }
    const handleRescedual = () => {
        setState({...state , showStartSession : false , showRescedual : true});
    }
    const handleCloseRescedual = () => {
        setState({...state , showRescedual : false});
    }
    const handleOnClickReschedual = () => {
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            let oldId = data.id;
            let newSession = [];
            if(state.choosenDate && state.choosenTime){
                setState({...state , loading : true })

                state.sessionsForReschedual.forEach(arrOfSession => {
                        let temp = arrOfSession.filter(session => session.date.toString() === state.choosenDate.toString());
                        if(temp.length){
                            newSession = temp
                        }
                });
                $.ajax({
                    url: Url,
                    method: 'post',
                    headers: {
                        authorization: `Bearer ${token}`
                    },
                    data: {
                        query: SESSION_RESCEDUAL,
                        variables : { 
                            id_old : oldId ,
                            id_new : newSession[0].id
                        }
                    },
                    dataType: 'json',
                    success: res => { 
                        if(res.data.sessionsChange.Status.check === true){
                        props.getUpcomingSessions();
                        setState({...state , showRescedual : false , loading : false  })
                        }
                    },
                    cache: err => console.log(err)
                });
            }
        else{
                setState({...state , error : 'Please make sure you enter the date and time'})
            }
        }
    }   
    const handleChange = props => event => {
        if(props == 'choosenDate'){
            let arrOfTimes = [] 
            state.sessionsForReschedual.forEach(arrOfSession => {
                let temp =  arrOfSession.filter(session => session.date.toString() === event.target.value.toString());
                if(temp.length){
                    arrOfTimes = temp
                }
            });
            setState({
                ...state,[props]: event.target.value,arrOfTimes : arrOfTimes
                , error : ''
                });
        }else{
            setState({
            ...state,
            [props]: event.target.value,  error : ''
            });
        }
      };
    
    return(
    <section className="col-md-3 mb-3 col-6">
        <div>
            <Loader
                className="loading"
                visible={state.loading}
                type="TailSpin"
                color="white"
                height={50}
                width={50}
            />
        </div>
        <button className="border-0 bg-transparent"  onClick={e=>setState({...state , showStartSession : true})}>
            <figure className="mb-1 text-left">
                <img style={{height : '246px'}} src={data.instructor.profile_image? data.instructor.profile_image.url : Pp}  alt="instractor" className="rounded img-fluid"/>
            </figure>
            <article className="row m-0 align-items-center">
                <div className="col-12 pl-0 text-left">
                    <p className="mb-0">{data.instructor.first_name} {data.instructor.last_name}</p>
                    <p style={{fontSize : '14px'}} className="mb-0 text-green d-inline-block pr-3">{data.instructor.courses_type.name} </p>
                    <p className="text-green mb-0 d-inline-block">
                        <img  src={clock} alt="clock"/>{data.time}</p>
                </div>
                {/* <div className="col-2 text-right p-0"><img src={more} alt=""/></div> */}
            </article>
       </button>
        <PopUp
            title={<Row className="m-0 align-items-center">
                <Col xs={3} className="p-0">
                    <img height="64px" width="64px" className=" rounded-circle" src={data.instructor.profile_image?.url} alt="instractor"/>
                </Col>
                <Col xs={9} className="pr-0">
                    <p className="mb-0 text-capitalize text-dark " style={{fontSize : '16px' ,  fontWeight : 'normal'}}>{data.instructor.first_name} {data.instructor.last_name}</p>
                    <p className="text-grey mb-0" style={{fontSize : '11px' , fontWeight : 'normal'}}>{data.instructor.email}</p>

                </Col>
            </Row>}
            show={state.showStartSession}
            hide={handleCloseStartSession}
            cancelBtn={textPage.cancelReservationBtn}
            cancelFun={handleCancelReservedSession}
            saveBtn={textPage.startBtn}
            saveFun={handleStartSession}
            saveDisabled={data.can_start}
            editBtn={textPage.rescedual}
            editFun={handleRescedual}
            content={
                <div>
                    <Row className="border-bottom pt-2 pb-2 m-0">
                        <Col className="text-left bold text-dark" xs={6}>{textPage.date}</Col> <Col xs={6}>{data.date}</Col>
                    </Row>
                    <Row className="border-bottom pt-2 pb-2 m-0">
                        <Col className="text-left bold text-dark" xs={6}>{textPage.time}</Col> <Col xs={6}>{data.time}</Col>
                    </Row>
                    <Row className="border-bottom pt-2 pb-2 m-0">
                        <Col className="text-left bold text-dark" xs={6}>{textPage.duration}</Col> <Col xs={6}>{data.rate_minutes_type.name}</Col>
                    </Row>
                    <Row className="pt-2 pb-2 m-0">
                        <Col className="text-left bold text-dark" xs={6}>{textPage.CourseType}</Col> <Col xs={6}>{data.course_type.name}</Col>
                    </Row>
                    {data.note && 
                        <Row className="pt-2 pb-2 m-0 bg-lightGreen">
                            <Col className="text-left text-green bold text-dark" xs={12}>{textPage.notes}</Col> 
                            <Col xs={12}>{data.note}</Col>
                        </Row>}
                </div>
            }
        />
        <PopUp 
            title={textPage.rescedual}
            show={state.showRescedual}
            hide={handleCloseRescedual}
            cancelBtn={textPage.cancelBtn}
            cancelFun={handleCloseRescedual}
            saveBtn={textPage.rescedual}
            saveFun={handleOnClickReschedual}
            content={
                <div>
                    <Row className="pt-2 pb-2 m-0">
                        <Col className="text-left  text-dark p-0" xs={12}>
                            <p  className="mb-0">{textPage.reservationType}</p>
                        </Col>
                        <Col className="bg-darkgray text-grey p-2" xs={12}>{data && data.course_type.name}</Col>
                    </Row>
                    <Row>
                        <Col md={12} className="">
                            <InputLabel fullWidth>
                                {textPage.date}
                            </InputLabel>
                            <FormControl
                                className=""
                                variant="outlined" fullWidth size="small" disabled={state.arrOfDates?.length > 0 ? false : true}>
                                <Select 
                                    native
                                    required
                                    value={state.choosenDate}
                                    onChange={handleChange('choosenDate')}
                                    >
                                    {(state.arrOfDates?.length > 0) && 
                                        <option aria-label="None" value="" />
                                    }
                                    {state.arrOfDates?.map(date=>
                                        <option value={date}>{date}</option>
                                    )}
                                    
                                </Select>
                            </FormControl>
                        </Col>
                    </Row>
                    <Row className=" pt-2 pb-2 m-0">
                        <Col className="text-left  text-dark p-0" md={6}>
                            <InputLabel fullWidth className="text-dark" >{textPage.time}</InputLabel>
                            <FormControl variant="outlined" fullWidth  size="small" disabled={state.arrOfTimes?.length > 0 ? false : true}>
                                <Select 
                                    native
                                    required
                                    value={state.choosenTime && state.choosenTime}
                                    onChange={handleChange('choosenTime')}
                                >
                                {(state.arrOfTimes?.length > 0) && 
                                    <option aria-label="None" value="" />
                                }
                                {state.arrOfTimes?.map(ele=>
                                     <option value={ele}>{ele.time}</option>    
                                )}
                                </Select>
                            </FormControl>
                        </Col>
                        <Col  className="bg-grey p-0" md={6}>
                            <p  className="mb-0 mr-2 text-dark">{textPage.duration}</p>
                            <p  className="bg-darkgray text-grey ml-2 p-2">{data && data.rate_minutes_type.name}</p>
                        </Col>
                        <Col xs={12} className="p-0">
                            <p className="text-danger">{state.error}</p>
                        </Col>
                    </Row>
            </div>}
        />
    </section>
    )
}